/**
 *   Name: BizAgi Render Image Class
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will redefine the image render class to adjust to tablet devices
 */

// Extends itself
//noinspection JSValidateTypes
bizagi.rendering.image.extend("bizagi.rendering.image", {
    QUALITY_PICTURE: 50,
    EXTENSIONSIMG: ["jpeg", "jpg", "png"]
}, {

    /**
     * Add an image
     * @param useCamera boolean Whether or not to use device's camera
     */
    imageAdd: function (useCamera) {
        var self = this;
        var imageEditorParams = bizagi.override && bizagi.override.mobileImageEditorParams ? bizagi.override.mobileImageEditorParams : {};
        var source = useCamera ? "camera" : "library";

        //1. if image editor is not supported, behaves as before
        //2. if image editor is supported, but there are no client-side overrides, image editor will appear
        //3. if image editor is supported, there are client-side overrides and the image editor is enabled, image editor will appear
        //4. if image editor is supported, there are client-side overrides but the image editor is disabled, behaves as before

        if (!bizagi.util.isNativeImageEditorSupported() || (bizagi.util.isNativeImageEditorSupported() && typeof(imageEditorParams.enableImageEditor) != "undefined" && !imageEditorParams.enableImageEditor)) {
            self._super(useCamera);
            return;
        }

        $.when(self.openImageEditor(source, imageEditorParams)).done(function (dataImage) {
            $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).done(function () {
                bizagi.util.media.getFileDataInfo(dataImage).then(function (fileData) {
                    if (bizagi.util.media.checkFileTypes(fileData, self.properties, self.Class.EXTENSIONSIMG)) {
                        self.dataImage = dataImage;
                        self.fileProperties = fileData;
                        $.when(self.uploadFile(self.dataImage)).done(function (result) {
                            self.uploadFileCompleted(self, result);
                        }).fail(function (error) {
                            self.executeFormFailHandler(error);
                        }).always(function () {
                            bizagi.loading.stop()
                        });
                    }
                });
            });
        }).fail(function (error) {
            bizagi.showMessageBox(error);
        });
    },

    /**
     * Open a native image editor to crop, resize and apply filters
     * @param source string to identify if image is loaded from library or camera
     */
    openImageEditor: function (source, imageEditorParams) {
        var self = this;
        var defer = new $.Deferred();
        imageEditorParams.source = source;
        imageEditorParams.imageMaxSize = self.properties.maxSize;

        bizagiapp.openImageEditor(imageEditorParams,
            function (response) {
                defer.resolve(JSON.parse(response).imageURL);
            },
            function (error) {
                defer.reject("Something went wrong loading the image.");
            });

        return defer.promise();
    }

});
