﻿/*
 *   Name: Bizagi Tablet Render upload Extension
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will redefine the label render class to adjust to smartphones devices
 */

// Extends itself
bizagi.rendering.upload.extend("bizagi.rendering.upload", {
    BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
    BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
    QUALITY_PICTURE: 50,
    LIMIT: 1, // limit: The maximum number of multimedia files in the device user can record in a single capture operation.
    EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
    EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
    EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
    EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"]
}, {

    /**
     * Template method to implement in each children to customize each control     
     */
    renderControl: function() {
        var self = this;
        var properties = self.properties;
        var template = self.renderFactory.getTemplate("upload");
        var allow_upload = true;
        var items = "";
	
        // Check files extensions
        self.checkExtensions();

        if (bizagi.util.isCordovaSupported()) {
            allow_upload = self.validOptions && (self.validOptions.image || self.validOptions.video);
        }

        // Render template
        var html = $.kendoTmpl(template, {
            xpath: bizagi.util.encodeXpath(self.getUploadXpath()),
            editable: properties.editable && allow_upload,
            noFiles: (self.filesCount == 0),
            allow_upload: allow_upload,
            nonSupportedExtension: self.nonSupportedExtension,
            allowSendInMail: properties.allowSendInMail
        });

        // Render current children       
        for (var i = 0; i < self.filesCount; i++) {
            var file = { id: self.files[i][0], fileName: self.files[i][1] };
            var item = self.renderUploadItem(file);
            items += item;
        }

	html = self.replaceFilesHtml(html, items);
        return html;
    },


    postRender: function () {
        var self = this;
        var properties = self.properties;

        // Call base
        self._super();

        if ((!bizagi.util.isCordovaSupported() && !bizagi.util.media.fileAPISupported()) || !properties.editable) {
            self.addEventToOpenSlide();
        } else if (!bizagi.util.isCordovaSupported()) {
            self.activeUploadWebControl();
        } else {
            self.activateUploadNative();
        }

        // Handler to download the file
        $("ul.files", self.getControl()).on("click", ".ui-bizagi-render-link.button", function (e) {
            bizagi.loading.start();
            $.when(bizagi.util.media.downloadFile($(this).data("url"), this.title)).fail(function (error) {
                self.executeFormFailHandler(error);
            }).always(function () {
                bizagi.loading.stop();
            });
        });
    },

    /**
    * Render readonly
    * @returns {}
    */
    postRenderReadOnly: function () {
        var self = this;
        var container = self.getControl();

        // Call base
        self._super();

        if (!bizagi.util.isOfflineForm({ context: self }) && typeof (self.files) !== "undefined" && self.files.length > 0) {
            var filesLinkElements = container.find(".ui-bizagi-render-link");

            filesLinkElements.on("click", function () {
                bizagi.loading.start();
                $.when(bizagi.util.media.downloadFile($(this).data("url"), this.title)).fail(function (error) {
                    self.executeFormFailHandler(error);
                }).always(function () {
                    bizagi.loading.stop();
                });
            });
        }
    },

    /**
    * Attach open slide
    * @returns {}
    */
    addEventToOpenSlide: function () {
        var self = this;
        var mode = self.getMode();
        var container = self.getControl();

        self.itemAddfile = $(".bz-rn-upload-show-menu", container);
        self.itemAddfile.hide();

        if (!bizagi.util.isCordovaSupported() && !bizagi.util.media.fileAPISupported()) {
            self.element.addClass("bz-rn-read-only");
        }

        $("li", container).click(function (event) {
            if (mode === "execution") {
                bizagi.loading.start();
                // Avoid redirect & download
                event.preventDefault();
                event.stopPropagation();

                var title = this.title || $(this).data("name");

                $.when(bizagi.util.media.downloadFile($(this).data("url"), title)).fail(function (error) {
                    self.executeFormFailHandler(error);
                }).always(function () {
                    bizagi.loading.stop();
                });
            }
        });
    },

    /**
    * Active upload by native mode
    * @returns {}
    */
    activateUploadNative: function () {
        var self = this;
        var properties = self.properties;
        var container = self.getControl();

        self.renderControl();
        self.itemAddfile = $(".bz-rn-upload-show-menu", container);

        if (!bizagi.util.isCordovaSupported() || !properties.editable) {
            self.itemAddfile.hide();
        }

        // Check files number
        self.checkMaxFiles();

        // Attach delete option
        self.attachDeleteHandler();

        // Not has valid extensions
        if (self.validOptions && (!self.validOptions.image && !self.validOptions.video)) {
            $("> *", self.itemAddfile).hide();
            return;
        }

        var uploadTemplate = self.renderFactory.getTemplate("uploadModalView");
        var kendoTemplate = kendo.template(uploadTemplate, { useWithBlock: false });

        // Check is offline form
        var isOfflineForm = bizagi.util.isOfflineForm({ context: self });

        // Creating modal view
        var $modalView = $.trim(kendoTemplate({
            editable: self.properties.editable,
            noFiles: self.properties.noFiles,
            displayName: self.properties.displayName || "",
            validExtensions: self.validOptions,
            isOfflineForm: isOfflineForm
        }));

        self.itemAddfile.bind("click", function (e) {
            var modalView = new kendo.mobile.ui.ModalView($modalView, {
                close: function() {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});

                    this.destroy();
                    this.element.remove();
                },
                modal: false,
                useNativeScrolling: true
            });

            self.configureModalViewHandlers(modalView.element);

            self.triggerGlobalHandler("modalViewDidAppear", {});
            modalView.open();
        });
    },

    /**
    * Configure the modalView Handlers for the new combo control.
    * @param {} inputContainer
    * @returns {}
    */
    configureModalViewHandlers: function (inputContainer) {
        var self = this;

        // Getting combo list elements
        self.listOptions = inputContainer.find(".bz-rn-upload-options li");

        // Setting the initial selected display value
        self.listOptions.bind("click", function () {
            self.applyPlugin($(this).data("role"), this.getBoundingClientRect());
        });

        inputContainer.find(".bz-wp-modalview-close").bind("click", function () {
            if (inputContainer.data("kendoMobileModalView")) {
                inputContainer.data("kendoMobileModalView").close();
            }
        });
    },

    /**
    * Attach delete option
    * @returns {}
    */
    attachDeleteHandler: function () {
        var self = this;
        var control = self.getControl();

        $(".bz-rn-upload-delete-icon", control).bind("click", function () {
            var item = $(this).parent(".ui-bizagi-render-upload-item");
            var file = item.data();

            $.when(self.deleteUploadItem(item, file.id))
                .done(function () {
                    // Remove item
                    item.hide();
                    item.remove();

                    // Update value
                    self.setValue(self.files, false);

                    // Check maxFiles
                    self.checkMaxFiles();

                    // Trigger change
                    self.triggerRenderChange();
                });
        });
    },

    /**
    * Apply plugin
    * @param {} option
    * @param {} coordinates
    * @returns {}
    */
    applyPlugin: function (option, coordinates) {
        var self = this;
        var resolutionUpload;

        // If android version is less than 4.4 avoid to show upload sizes due to compatibility with blob files
        var version = bizagi.util.getAndroidVersion();
        var hideUploadSizes = version && version <= 4.4;

        // Resolution upload menu
        if (hideUploadSizes) {
            resolutionUpload = [
                { "guid": 4, "displayName": bizagi.localization.getResource("workportal-size-original") }
            ];
        } else {
            resolutionUpload = bizagi.util.media.getImageResolutionItems();
        }

        // Attach document
        $(".bz-resolution-container", self.getControl()).actionSheet({
            actions: resolutionUpload,
            actionClicked: function (action) {
                bizagi.loading.start();

                $.when(self.resolutionButtonHandler(action.guid)).always(function () {
                    bizagi.loading.stop();
                });
            },
            appear: function (action) {
                self.triggerGlobalHandler("modalViewDidAppear", {});
            },
            cancelClicked: function (action) {
                self.triggerGlobalHandler("modalViewDidDisappear", {});
            }
        });

        switch (option) {
            case "image":
                // Check is offline form
                var isOfflineForm = bizagi.util.isOfflineForm({ context: self });

                // Select image
                var popover = new CameraPopoverOptions(coordinates.left, coordinates.top - 20, 200, 100, 4);
                var options = {
                    quality: self.Class.QUALITY_PICTURE,
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    popoverOptions: popover
                };

                navigator.camera.getPicture(function (dataImage) {
                    self.dataImage = dataImage;
                    var extensions = self.Class.EXTENSIONSIMG
                        .concat(self.Class.EXTENSIONSVIDEO);

                    bizagi.loading.start();
                    self.closeModalView();

                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties))
                        .then(function () {
                            return $.when(bizagi.util.media.getFileDataInfo(self.dataImage))
                                .then(function (file) {

                                    self.fileProperties = file;

                                    if (bizagi.util.media.checkFileTypes(file, self.properties, extensions)) {
                                        // "new Blob" constructor is not supported in android version 4.2.2
                                        var version = bizagi.util.getAndroidVersion();

                                        if ((version && version <= 4.2) || isOfflineForm) {
                                            return self.saveImage(self, self.dataImage);
                                        } else {

                                            if (!bizagi.util.isAndroid()) {
                                                return $.when(bizagi.util.media.modalResolutionUpload()).then(
                                                    function (buttonIndex) {
                                                        if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                                                            return self.resolutionButtonHandler(buttonIndex);
                                                        }
                                                    });

                                            } else {
                                                // Force click to show actionsheet menu
                                                $(".bz-resolution-container", self.getControl()).click();
                                            }
                                        }
                                    }
                                })
                        }).always(function () {
                            bizagi.loading.stop();
                        });
                }, self.onFail.bind(self), options);
                break;

            case "file":
                // TODO: Add handlers to upload web                
                break;

            case "cimage":
                navigator.camera.getPicture(function (dataImage) {
                    bizagi.loading.start();
                    self.closeModalView();

                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).then(function () {
                        return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (file) {
                            self.fileProperties = file;
                            return self.saveImage(self, dataImage);
                        });
                    }).always(function () {
                        bizagi.loading.stop();
                    });
                }, self.onFail.bind(self), {
                    quality: self.Class.QUALITY_PICTURE,
                    targetWidth: 1280,
                    targetHeight: 960,
                    correctOrientation: true
                });
                break;

            case "cvideo":
                navigator.device.capture.captureVideo(function (dataImage) {
                    bizagi.loading.start();
                    self.closeModalView();

                    $.when(bizagi.util.media.checkMaxSizeVideo(dataImage, self.properties)).then(function () {
                        return self.saveVideo(self, dataImage);
                    }).always(function () {
                        bizagi.loading.stop();
                    });
                }, self.onFail.bind(self), { limit: self.Class.LIMIT });
                break;

            default:
                break;
        }
    },

    closeModalView: function () {
        var self = this;
        var modalView = self.listOptions.closest(".modalview-upload-options");

        if (self.listOptions && modalView) {
            if (modalView.data("kendoMobileModalView")) {
                modalView.data("kendoMobileModalView").close();
            }
        }
    },

    resolutionButtonHandler: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;

        $(".ui-bizagi-resolution-container").css("display", "none");
        $(".ui-bizagi-mask").remove();

        // If original, save image normally, if not, reduce quality and size
        if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
            return self.saveImage(self, self.dataImage);
        } else {
            var resolution = bizagi.util.media.getImageResolution(actionSelected);

            return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution))
                .then(function (result) {
                    self.editedImageURL = result.localUrl;
                    self.properties.url = result.base64Image;

                    return self.saveImage(self, self.dataImage);
                });
        }

    },

    /**
    * Call the form failhandler with an specific error
    * @param {} error
    * @returns {}
    */
    executeFormFailHandler: function (error) {
        var self = this;
        var errorResponse = error.responseText ? error.responseText : bizagi.localization.getResource("workportal-general-error");
        self.getFormContainer().failHandler(errorResponse);
    },

    renderUploadItem: function (file) {
        var self = this;
        var properties = self.properties;
        var mode = self.getMode();
        var template = self.renderFactory.getTemplate("uploadItem");

        // Don't render urls when not running in execution mode
        var url = mode !== "execution" ? "javascript:void(0);" : self.buildItemUrl(file);

        var html = $.kendoTmpl(template, {
            url: url,
            image: false,
            allowDelete: properties.allowDelete,
            editable: properties.editable,
            filename: file.fileName,
            id: file.id,
            mode: mode,
            isNativeContext: bizagi.util.isNativePluginSupported(),
            isCordovaSupported: bizagi.util.isCordovaSupported()
        });

        return html;

    },

    getTemplateName: function () {
        return "upload";
    },

    getTemplateItemName: function () {
        return "uploadItem";
    },

    getTemplateEditionName: function () {
        return "edition.upload";
    },

    getTemplateEditionMenu: function () {
        return "edition.upload.menu";
    },

    /**
    * Send image using HTTP
    * @param {} context
    * @param {} dataImage
    * @returns {}
    */
    saveImage: function (context, dataImage) {
        var self = context;
        var params = {};
        var addParams = self.buildAddParams();
        var fileName = self.fileProperties.name;

        if (self.editedImageURL) {
            params.dataFile = self.editedImageURL;
        } else {
            params.dataFile = self.fileProperties.url;
        }

        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());
        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileKey = "file";
        params.options.fileName = fileName;
        params.options.mimeType = "image/jpeg";
        params.options.uploadKey = { key: addParams["h_xpath"] };
        params.options.addUrl = self.properties.addUrl;

        //Cleans variables related previous gallery uploads
        self.editedImageURL = null;
        self.fileProperties = null;

        // Fix android 4.4 getting images from recent folder
        params.dataFile = bizagi.util.media.getImagePath(params.dataFile);

        return $.when(bizagi.util.media.uploadFile(params)).done(function (r) {
            var response = JSON.parse(decodeURIComponent(r.response));
            if (response.type === "error") {
                bizagi.showMessageBox(response.message, response.errorType);
            } else {
                self.onUploadFileCompleted(context, response);
            }
        }).fail(function (error) {
            var response = JSON.parse(decodeURIComponent(error.responseText));
            bizagi.showMessageBox(response.message, response.type);
        });
    },

    /**
    * saveVideo
    * @param {} context
    * @param {} dataVideo
    * @returns {}
    */
    saveVideo: function (context, dataVideo) {
        var self = context;
        var params = {};
        var addParams = self.buildAddParams();

        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());

        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileName = dataVideo[0].name;
        params.options.mimeType = "video/quicktime";
        params.options.uploadKey = { key: addParams["h_xpath"] };
        params.options.addUrl = self.properties.addUrl;
        params.dataFile = dataVideo[0].fullPath;

        return $.when(bizagi.util.media.uploadFile(params)).done(function (r) {
            var response = JSON.parse(decodeURIComponent(r.response));
            if (response.type === "error") {
                bizagi.showMessageBox(response.message, response.errorType);
            } else {
                self.onUploadFileCompleted(context, response);
            }
        });
    },

    /**
     * Management fail messages
     */
    onFail: function (error) {                
        var messageError = bizagi.util.processFailMessage(error);                

        if (typeof (messageError) == "string" && messageError.indexOf("No Activity found to handle Intent") !== -1) {
            var hasResource = bizagi.util.isValidResource("render-upload-error-media-app");
            var message = hasResource ? bizagi.localization.getResource("render-upload-error-media-app") : "Your device does not have an app to execute this action.";

            bizagi.showMessageBox(message);
        }
    },

    onUploadFileCompleted: function (context, result) {
        var self = context;
        var control = self.getControl();
        var uploadWrapper = $("ul.files", control);                

        if (result.id && result.fileName) {
            // Locate it before the upload wrapper
            var newItem = self.renderUploadItem(result);
            $(uploadWrapper).append(newItem);

            // Increment counter
            self.filesCount = self.filesCount + 1;

            // Copy the files value but not its reference
            var files = self.files.slice();

            //Add the new item to this temporal array
            files.push([result.id, result.fileName]);

            // Update value
            self.setValue(files, true);

            // Check maxFiles
            self.checkMaxFiles();

            control.find(".ui-bizagi-render-upload-item-no-upload").hide();

            //Add delete functionality just for elements included in common forms, not inside columns
            if (self.properties.type !== "columnUpload" && (self.properties.allowDelete && self.properties.editable)) {
                self.attachDeleteHandler();
            }

            self.editedImageURL = null;
        } else {            
            bizagi.showMessageBox(result.message, "Error");
        }
    },   

    /**
    * Checks if the total files uploaded reachs the maximun supported
    * @returns {}
    */
    checkMaxFiles: function () {
        var self = this;
        var properties = self.properties;
        var control = self.getControl();
        var maxFiles = properties.maxfiles;
        var actualFiles = properties.value ? properties.value.length : 0;

        if (properties.editable) {
            if (maxFiles !== 0 && ((self.filesCount >= maxFiles && actualFiles >= maxFiles))) {
                self.itemAddfile.hide();
            } else {
                self.itemAddfile.show();
            }
        } else {
            //Checks if the files count is equals to 0, to show the no files dialog
            if (self.filesCount == 0) {
                $("#no-files", control).show();
            }
        }
    },

    /**
    * Checks the file Extension in each filetype that the device supports
    * @returns {}
    */
    checkExtensions: function() {
        var self = this;
        var validExtensions = self.properties.validExtensions || "";

        //Helps to get the non-supported extensions for mobile
        var nonSupportedExtensionsArr = [];

        // Remove spaces in the list
        validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();
        validExtensions = validExtensions.split(";");

        // Initizalice the nonSupportedExtension element
        self.nonSupportedExtension = "";

        self.validOptions = { video: false, image: false, file: false };

        if (typeof (validExtensions) === "undefined" || validExtensions[0] === "") {
            self.validOptions = { video: true, image: true, file: false };

            return;
        }

        if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) return;

        for (var index = 0, length = validExtensions.length; index < length; index++) {
            validExtensions[index] = validExtensions[index].replace("*.", "");

            /*if (self.Class.EXTENSIONSFILE.toString().indexOf(validExtensions[index]) >= 0) {
                self.validOptions.file = true;
            } else */
            if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[index]) >= 0) {
                self.validOptions.image = true;
            } else if (self.Class.EXTENSIONSVIDEO.toString().indexOf(validExtensions[index]) >= 0) {
                self.validOptions.video = true;
            } else {
                //If the extension is not part or any category, it is stored in nonSupportedExtensions array
                nonSupportedExtensionsArr.push(validExtensions[index]);
            }
        }

        // Adjust file extension, in order to make it multiline & Stores the non-supported extensions
        self.nonSupportedExtension = nonSupportedExtensionsArr.join(", ") || "";
    },

    /**
    * Active upload web control
    * @returns {}
    */
    activeUploadWebControl: function () {
        var self = this;
        var container = self.getControl();
        var uploadfile = $(".bz-rn-upload-web", container);
        var file = $(".bz-rn-upload-show-menu span.bz-cm-icon", container);

        self.itemAddfile = $(".bz-rn-upload-show-menu", container);

        // Check maximum files number
        if (self.itIsMaxNumberFilesReached()) {
            self.itemAddfile.hide();
        }

        self.itemAddfile.click(function () {
            uploadfile.click();
        });

        // attach events to file-icon
        file.addClass("bz-rn-upload-native");
        file.on("click", function () {
            uploadfile.click();
        });

        // Attach event to upload-control
        uploadfile.on("change", function() {
            var that = this;
            var dataFile = that.files[0];
            var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSFILE, self.Class.EXTENSIONSVIDEO);

            dataFile.fullPath = that.value;

            bizagi.loading.start();

            if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) && bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {

                $.when(self.processUploadFile(dataFile))
                    .then(function(result) {
                        self.onUploadFileCompleted(self, JSON.parse(decodeURIComponent(result)));
                    }).fail(function(error) {
                        if (error.statusText !== "Unauthorized" || error.statusText.toLowerCase() !== "ok") {
                            bizagi.showMessageBox(self.getResource("workportal-widget-admin-language-error"));
                        }
                    }).always(function() {
                        bizagi.loading.stop();
                    });
            } else {
                bizagi.loading.stop();
            }

            // Clearing input Values
            $(that).val("");
        });

        // Check files number
        self.checkMaxFiles();

        // Attach delete option
        self.attachDeleteHandler();
    },

    /**
    * Check the maximun file than can be uploaded
    * @returns {}
    */
    itIsMaxNumberFilesReached: function () {
        var self = this;
        var properties = self.properties;
        var maxFiles = properties.maxfiles;
        var actualFiles = properties.value ? properties.value.length : 0;

        return maxFiles !== 0 && (actualFiles >= maxFiles);
    },

    /**
    * Build add params to send to the server
    * @param {} dataFile
    * @returns {}
    */
    buildAddFileParams: function (dataFile) {
        var self = this;
        var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;
        var properties = self.properties;

        // Get form data for POSTing
        var formData = new FormData();
        var form = self.getFormContainer();

        // Build add params to send to the server
        formData.append(prefix + "xpath", self.getUploadXpath());
        formData.append(prefix + "idRender", properties.id);
        formData.append(prefix + "xpathContext", properties.xpathContext);
        formData.append(prefix + self.Class.BA_PAGE_CACHE, properties.idPageCache);
        formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
        formData.append(properties.xpath, dataFile);

        if (properties.contexttype) {
            formData.append(prefix + "contexttype", properties.contexttype);
        }

        try {
            if (typeof (BIZAGI_SESSION_NAME) !== "undefined") {
                formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
            } else {
                formData.append("JSESSIONID", form.properties.sessionId);
            }
        } catch (e) {
            formData.append("JSESSIONID", form.properties.sessionId);
        }

        return formData;
    },

    /**
    * Process an upload file into the server
    * @param {} context
    * @param {} dataFile
    * @returns {}
    */
    processUploadFile: function(dataFile) {
        var data = { url: this.properties.addUrl, formData: this.buildAddFileParams(dataFile) };
        return this.dataService.processUploadFile(data);
    },

    clearDisplayValue: function () {
        var self = this;
        var items = $(".ui-bizagi-render-upload-item", self.getControl());

        items.each(function (index, item) {
            item = $(item);
            var file = item.data();
            $.when(self.deleteUploadItem(item, file.id)).done(function () {
                // Remove item
                item.hide();
                item.remove();

                // Update value
                self.setValue(self.files, false);

                // Check maxFiles
                self.checkMaxFiles();

                // Trigger change
                self.triggerRenderChange();
            });
        });
    }
});